import React from "react"
import styled from "styled-components"
import { FaFolderOpen, FaCalendarDay } from "react-icons/fa"
import { Link } from 'gatsby'

const MainContainer = styled.div`
	display: inline-block;
	width: calc(100% - 30px);
	max-width: 330px;
	height: 400px;
	margin: 10px;
	position: relative;
	text-align: left;
	box-sizing: border-box;
	overflow: hidden;
	
	
	@media (max-width: 1079px) {
		max-width: 400px;
		height: 300px;
	}

	@media (max-width: 887px) {
		max-width: none;
	}

	&:hover {
		background-size: calc(cover * 1.2);
	}

`
const BackgroundContainer = styled.div`
	width: 100%;
	height: 100%;
	
	position: absolute;
	opacity: 0.5;
	background-image: url('${props => props.src}');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center top;
	transition: 0.4s;

	&:hover {
		transform: scale(1.2);
	}
`

const BottomContainer = styled.div`
	width: 100%;
	position: absolute;
	bottom: 0%;

	a {
		color: white;
		text-decoration: none;
	}
`

const CategoryWrapper = styled.span`
	margin-right: 4px;
	padding: 8px;
	background-color: black;
	a {
		color: white;
		text-decoration: none;
		font-family: Helvetica,Arial,sans-serif;
	}
	@media (max-width: 1079px) {
		padding: 6px;
		font-size: 14px;
	}
`

const DateConatiner = styled.div`
	padding: 8px;
`

const Title = styled.div`
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	font-size: 18px;
	font-weight: bold;
	padding: 8px;
	color: white;
	transition: 0.4s;	

	&:hover {
		color: #cccccc;
	}
`

const TimeContainer = styled.div`
	font-size: 14px;
	color: #cccccc;
	padding: 8px;
`

const Content = styled.div`
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	font-size: 14px;
	font-weight: 600;
	color: white;
	padding-left: 8px;
`

const BlackContainer = styled.div`
	z-index: -1;
	width: 100%;
	height: 100%;
	position: absolute;
	background-color: black;
	pointer-events: none;
`

interface PostPreview {
	title: string;
	tags: Array<string>;
	date: string;
	img: string;
	path: string;
	summary: string;
	key: string;
}

const PostBlock: React.FunctionComponent<PostPreview> = (props) => {
	const tags = props.tags.map(x => <CategoryWrapper key={x}><Link to={`/${x.toLowerCase()}`}>{x}</Link></CategoryWrapper>)
	
	return (
		
			<MainContainer>
				<Link to={`${props.path}`}>
					<BackgroundContainer src={`${props.img}`}/>
					<BlackContainer/>
				</Link>
				<BottomContainer>
					<div style={{"margin": "8px"}}>{tags}</div>
					<Link to={`${props.path}`}>
						<Title>{props.title}</Title>
						<Content>{props.summary}</Content>
						<TimeContainer><FaCalendarDay /> {props.date}</TimeContainer>
					</Link>
				</BottomContainer>
			</MainContainer>
		
	)
}

export default PostBlock;